.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


body{
  background-color: rgb(204, 204, 195);
}
.container{
  background-color: white;
  justify-content: center;
  width: 50%;
  margin-left: 26%;
  margin-top: 5%;
  text-align: center;
}
input[type=text], select{
  width: 80%;
  padding: 18px 20px;
  margin: 12px 0;
  display: inline-block;
  border: 1px solid rgb(36, 43, 145);
  border-radius: 4px;
  box-sizing: border-box;
}
input[type=password], select{
  width: 80%;
  padding: 18px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid rgb(36, 43, 145);
  border-radius: 4px;
  box-sizing: border-box;
}
button{
  width: 30%;
  background-color: green;
  color: white;
  font-size: 15px;
  padding: 18px 30px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
button:hover{
  background-color: rgb(14, 87, 81);
}
.header{
  width: 95%;
  background-color: blue;
  padding: 1px 20px;
  color: white;
}
.ui-define{
  background-color: rgb(104, 216, 231);
  margin-left: 26%;
  width: 46.5%;
  padding: 20px 30px;
  margin-top: 5%;
}